/*
// Progress Bar
// ================ */
.progress {
  display: block;
  overflow: visible;
}

.progress__wrapper {
  position: relative;
  text-align: left;
}

.progress__wrapper.no-anim .progress__bar {
  transition: none;
}

.progress__wrapper + .progress__wrapper {
  margin-top: 36px;
}

.progress__bar {
  height: 100%;
  width: 0;
  transition: width 1.4s ease;
  position: relative;
}

.progress__bar-counter:after {
  content: '%';
}

.progress__gradient + .progress__gradient {
  margin-top: 36px;
}

.progress__gradient .progress {
  height: 3px;
  background: #E8ECFF;
  margin-bottom: 21px;
}

.progress__gradient .progress__bar {
  background: var(--brand-primary);
  background: linear-gradient(to right, var(--brand-primary), var(--secondary));
  margin-bottom: 20px;
  position: relative;
}

.progress__gradient .progress__bar > .before {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: 7px;
  height: 14px;
  border-bottom-right-radius: 90px;
  border-top-right-radius: 90px;
  background: var(--brand-primary);
}

.progress__gradient .progress__bar-counter {
  text-align: right;
  display: block;
}

.progress__wide {
  padding-top: 35px;
}

@media (min-width: 768px) {
  .progress__wide {
    padding-top: 0;
  }
}

.progress__wide + .progress__wide {
  margin-top: 31px;
}

.progress__wide .progress {
  height: 42px;
  background: #F7FAFF;
  background-image: linear-gradient(-45deg, #fff 25%, transparent 25%, transparent 50%, #fff 50%, #fff 75%, transparent 75%, transparent);
  background-size: 1rem 1rem;
  border-radius: 50px;
  position: relative;
  z-index: 1;
}

.progress__wide .progress:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 2px solid #E9F1FF;
  border-radius: 50px;
  z-index: -1;
}

.progress__wide .progress__title {
  color: #222;
  text-align: left;
  position: absolute;
  top: -10px;
  transform: translateY(-100%);
  left: 0;
  z-index: 1;
}

@media (min-width: 768px) {
  .progress__wide .progress__title {
    color: #fff;
    top: 50%;
    transform: translateY(-50%);
    left: 28px;
  }
}

.progress__wide .progress__bar {
  background: var(--brand-primary);
  position: relative;
  border-radius: 50px;
  box-shadow: 0 5px 16px 0 rgba(var(--brand-primary-rgb), 0.5);
}

.progress__wide .progress__bar-counter {
  color: var(--brand-primary);
  position: absolute;
  top: 4px;
  bottom: 4px;
  right: 4px;
  background: #fff;
  border-radius: 50px;
  text-align: center;
  padding: 7px 12px;
}

.progress__doted + .progress__doted {
  margin-top: 45px;
}

.progress__doted .progress {
  height: 4px;
  background: #fff;
  margin-top: 13px;
  position: relative;
}

.progress__doted .progress > .after {
  content: '';
  position: absolute;
  width: 100%;
  height: 8px;
  left: 0;
  top: -1px;
  background-image: radial-gradient(ellipse, rgba(var(--brand-primary-rgb), 0.3) 1px, rgba(var(--brand-primary-rgb), 0.3) 1px, transparent 2px);
  background-size: 15px 19px;
  background-position: -6px -6px;
  background-repeat: repeat-x;
}

.progress__doted .progress__descr {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.progress__doted .progress__bar {
  position: relative;
}

.progress__doted .progress__bar > .after {
  content: '';
  position: absolute;
  width: 100%;
  height: 8px;
  left: 0;
  top: -1px;
  background-image: radial-gradient(ellipse, var(--brand-primary) 1px, var(--brand-primary) 1px, transparent 2px);
  background-size: 15px 19px;
  background-position: -6px -6px;
  background-repeat: repeat-x;
}

.progress__doted .progress__bar .circle {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: -10px;
  width: 20px;
  height: 20px;
  background: #E9F1FF;
  border-radius: 50%;
  z-index: 1;
}

.progress__doted .progress__bar .circle > .after {
  content: '';
  position: absolute;
  top: 6px;
  left: 6px;
  right: 6px;
  bottom: 6px;
  background: var(--brand-primary);
  border-radius: 50%;
}

.progress__doted .progress__bar-counter {
  color: var(--brand-primary);
  text-align: right;
  display: block;
}

.progress__stripe + .progress__stripe {
  margin-top: 30px;
}

.progress__stripe .progress {
  height: 26px;
  background: #fff;
  margin-top: 16px;
  position: relative;
  z-index: 1;
  background: #fff;
  border-radius: 50px;
}

.progress__stripe .progress:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 2px solid #E9F1FF;
  border-radius: 50px;
  z-index: -1;
}

.progress__stripe .progress__title {
  margin-left: 10px;
}

.progress__stripe .progress__bar {
  position: relative;
  background: var(--brand-primary);
  background-image: linear-gradient(-45deg, rgba(255, 255, 255, 0.3) 5%, transparent 5%, transparent 50%, rgba(255, 255, 255, 0.3) 50%, rgba(255, 255, 255, 0.3) 55%, transparent 55%, transparent);
  background-size: 6px 6px;
  background-repeat: repeat;
  border-radius: 50px;
  box-shadow: 0 5px 16px 0 rgba(var(--brand-primary), 0.5);
}

.progress__stripe .progress__bar-counter {
  color: var(--brand-primary);
  text-align: center;
  display: block;
  position: absolute;
  z-index: 2;
  width: 100%;
  padding: 6px;
  top: calc(50% + 1px);
  transform: translateY(-50%);
}

.progress__count + .progress__count {
  margin-top: 45px;
}

.progress__count .progress {
  height: 3px;
  margin-top: 9px;
  position: relative;
  background: #E9F1FF;
}

.progress__count .progress__descr {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.progress__count .count-value {
  display: inline-block;
  color: var(--brand-primary);
  font-size: 32px;
}

.progress__count .count-amount {
  display: inline-block;
  vertical-align: sub;
  color: #C8D8F3;
  font-size: 18px;
}

.progress__count .count-amount:before {
  content: '/';
  padding-right: 3px;
}

.progress__count .progress__bar {
  position: relative;
  background: var(--brand-primary);
}

.progress__count .progress__bar .circle {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: -10px;
  width: 14px;
  height: 14px;
  background: var(--brk-base-1);
  border-radius: 50%;
  z-index: 1;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.2);
}

.progress__count .progress__bar .circle:after {
  content: '';
  position: absolute;
  top: 4px;
  left: 4px;
  right: 4px;
  bottom: 4px;
  background: #fff;
  border-radius: 50%;
  box-shadow: 0 0 0 2px var(--brand-primary);
}

.progress__squared {
  display: flex;
